  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
    font-family: 'Circular Std';
    src: url(/movie-streaming/static/media/CircularStd-Medium.7a92b9df.woff2) format('woff2'),
