/** ----- Button style ----- **/
.theme-btn,
a.theme-btn {
  background: #104cba;
  font-size: 16px;
  color: white;
  cursor: pointer;
  font-weight: 500;
  text-align: center;
  border-radius: 5px;
  padding: 11px 28px;
  align-items: center;
  display: inline-flex;
  text-transform: capitalize;
}

.theme-btn i,
a.theme-btn i {
  transition: 0.5s;
  margin-left: 10px;
}

.theme-btn.btn-circle,
a.theme-btn.btn-circle {
  border-radius: 50px;
  padding-left: 35px;
  padding-right: 35px;
}

.theme-btn:hover,
a.theme-btn:hover {
  color: white;
  background: #000000;
}

.theme-btn:hover i,
a.theme-btn:hover i {
  margin-left: 15px;
  margin-right: -5px;
}

.theme-btn.style-two,
a.theme-btn.style-two {
  padding: 11px 35px;
  background: #0850b0;
  border: 1px solid #88a6dd;
}

.theme-btn.style-two:hover,
a.theme-btn.style-two:hover {
  background: #000000;
  border-color: #000000;
}

.theme-btn.style-three,
a.theme-btn.style-three {
  padding: 11px 40px;
  color: #104cba;
  background: #e5eef6;
}

.theme-btn.style-three:hover,
a.theme-btn.style-three:hover {
  color: #e5eef6;
  background: #104cba;
}

.theme-btn.style-four,
a.theme-btn.style-four {
  padding: 11px 35px;
  background: #24323d;
}

.theme-btn.style-four:hover,
a.theme-btn.style-four:hover {
  background: #104cba;
}

.theme-btn.style-five,
a.theme-btn.style-five {
  padding: 10px 35px;
  background: transparent;
  border: 1px solid white;
}

.theme-btn.style-five:hover,
a.theme-btn.style-five:hover {
  background: white;
  color: #104cba;
}

.theme-btn.style-six,
a.theme-btn.style-six {
  padding: 10px 35px;
  color: #000000;
  background: transparent;
  border: 1px solid #e5e5e5;
}

.theme-btn.style-six:hover,
a.theme-btn.style-six:hover {
  background: #e5eef6;
}

.theme-btn.style-seven,
a.theme-btn.style-seven {
