}

.theme-btn.style-eleven i,
a.theme-btn.style-eleven i {
  color: #000000;
}

.theme-btn.style-eleven:hover,
a.theme-btn.style-eleven:hover {
  color: white;
  background: #F43336;
}

.theme-btn.style-eleven:hover i,
a.theme-btn.style-eleven:hover i {
