}

.theme-btn.style-twelve:hover,
a.theme-btn.style-twelve:hover {
  background: white;
  color: #20C997;
}

.theme-btn.prev-icon i,
a.theme-btn.prev-icon i {
  margin-left: 0;
  margin-right: 10px;
}

.theme-btn.prev-icon:hover i,
a.theme-btn.prev-icon:hover i {
