/*** ----- Preloader style ----- ***/
.preloader {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: #ffffff;
  z-index: 9999999;
}

.preloader .theme-loader {
  margin: auto;
  height: 70px;
  width: 70px;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 999;
  position: absolute;
  transform-style: preserve-3d;
  animation: animate 4.59s ease-in-out infinite;
}

.preloader .theme-loader:before,
.preloader .theme-loader:after {
  content: "";
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: absolute;
  border: 15px solid #1B76FF;
  animation: animate_1 1.73s ease-in-out infinite both reverse;
}

.preloader .theme-loader:after {
  border-color: #0D47A1;
  animation-delay: -0.86s;
}

/* ----- Pagination ----- */
.pagination li {
  margin: 0 10px 10px 0;
}

.pagination li a,
.pagination li .page-link {
  padding: 0;
  color: #859aaa;
  box-shadow: none;
  width: 60px;
  height: 60px;
  background: white;
  line-height: 60px;
  border-radius: 50%;
  text-align: center;
  border: 1px solid #e5e5e5;
}

@media only screen and (max-width: 480px) {

  .pagination li a,
  .pagination li .page-link {
    width: 45px;
    height: 45px;
    line-height: 45px;
  }
}

.pagination li.disabled .page-link,
.pagination li:last-child .page-link {
  border-radius: 50%;
}

.pagination li:hover:not(.disabled) .page-link,
.pagination li.active .page-link {
  color: white;
  background: #104cba;
  border-color: #104cba;
}

