/* -------------------------------------------------------------- */

/* Import Google Fonts */
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;700;800&family=Rubik:wght@400;500&family=Oswald:wght@300;400;500;600;700&family=Mulish:wght@400;500;600&display=swap");

/* @import url("../fonts/customFonts.css"); */

/*******************************************************/
/******************* 01. Default Style *****************/
/*******************************************************/

* {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

body {
  color: #454545;
  background: white;
  font-weight: 400;
  line-height: 28px;
  font-size: 16px;
  font-family: "Rubik", sans-serif;
  overflow-y: auto;
  overflow-x: hidden;
}

a {
  color: #454545;
  cursor: pointer;
  outline: none;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  text-decoration: none;
}

a:hover,
a:focus,
a:visited {
  text-decoration: none;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 12px;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: #000000;
}

.text-light-black {
  color: #0e1e2a !important;
}

.text-white h1,
.text-white h2,
.text-white h3,
.text-white h4,
.text-white h5,
.text-white h6,
.text-white h1 a,
.text-white h2 a,
.text-white h3 a,
.text-white h4 a,
.text-white h5 a,
.text-white h6 a {
  color: white;
}

h1 {
  font-size: 72px;
}

h2 {
  line-height: 1.33;
  font-size: 36px;
}

h3 {
  line-height: 1.55;
  font-size: 22px;
}

h4 {
  line-height: 1.4;
  font-size: 20px;
}

h5 {
  font-size: 18px;
}

h6 {
  font-size: 16px;
}

p {
  color: #454545;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  display: inline-block;
}

header:after,
section:after,
footer:after {
  display: block;
  clear: both;
  content: "";
}

.transition {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.text-white {
  color: #fff;
}

/*======= ScrollUp =======*/

.scrollUpBtn {
  text-align: center;
  cursor: pointer;
  position: fixed;
  z-index: 99;
  border: none;
  transition: 0.4s;
  bottom: 25px;
  font-size: 16px;
  right: 25px;
  text-decoration: none;
  animation: pulse 2s infinite;
  background: #135be0;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  -webkit-animation: pulse 2s infinite;
  animation: pulse 2s infinite;
}

.scrollUpBtn i {
  font-size: 18px;
  color: #fff;
  position: relative;
  top: 2px;
}

/*======= Input Styles =======*/

input,
select,
textarea,
.form-control {
  width: 100%;
  height: auto;
  padding: 16px 30px;
  border-radius: 5px;
  background-color: #fff;
  border: 2px solid #cfdbf1;
}

/* ----- Input Type Number Remove Arrows/Spinner ----- */

.no-arrows::-webkit-outer-spin-button,
.no-arrows::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.no-arrows[type=number] {
  -moz-appearance: textfield;
  appearance: textfield;
}

textarea {
  display: inherit;
  padding-top: 20px;
}

label {
  cursor: pointer;
  font-weight: 500;
  margin-bottom: 5px;
  color: #000000;
}

input:focus,
button:focus,
.form-control:focus {
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  border-color: #cfdbf1;
}

input[type=search]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

input[type=search]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

input[type=checkbox],
input[type=radio] {
  height: auto;
  width: auto;
}

.text-white input,
.text-white select,
.text-white textarea,
.text-white .form-control {
  color: #ffffff50;
  border-color: #273540;
  background: #0e1e2a;
}

.text-white label {
  margin-bottom: 10px;
}

