/** ----- Button style ----- **/
.theme-btn,
a.theme-btn {
  background: #104cba;
  font-size: 16px;
  color: white;
  cursor: pointer;
  font-weight: 500;
  text-align: center;
  border-radius: 5px;
  padding: 11px 28px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  text-transform: capitalize;
}

.theme-btn i,
a.theme-btn i {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  margin-left: 10px;
