/******************* 05. Header style ******************/
/*******************************************************/
.main-header {
  position: relative;
  left: 0px;
  top: 0px;
  z-index: 999;
  width: 100%;
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
}

.main-header .header-upper {
  z-index: 5;
  width: 100%;
  position: relative;
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
}

.main-header .header-upper .container-fluid {
  padding: 0;
}

@media only screen and (max-width: 991px) {
  .main-header .logo-outer {
    display: none;
  }
}

.main-header .logo {
  z-index: 9;
  padding: 2px 0;
  position: relative;
}

.nav-outer {
  width: 100%;
  position: relative;
}

@media only screen and (max-width: 991px) {
  .nav-outer {
    position: static;
  }
}

.nav-outer .menu-btn {
  margin-left: auto;
}

@media only screen and (max-width: 991px) {
  .nav-outer .menu-btn {
    display: none;
  }
}

/** Header Main Menu **/
@media only screen and (max-width: 991px) {
  .main-menu {
    width: 100%;
  }
}

@media only screen and (max-width: 480px) {
  .main-menu .mobile-logo {
    max-width: 150px;
  }
}

@media only screen and (max-width: 991px) {
  .main-menu .collapse {
    overflow: auto;
  }
}

.main-menu .navbar-collapse {
  padding: 0px;
}

@media only screen and (min-width: 992px) {
  .main-menu .navbar-collapse ul li:hover>ul {
    display: block;
  }

}



@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse>ul {
    margin: 25px 0;
    max-height: 80vh;
    border-bottom: 1px solid #f3f3f3;
    /* contact border lagbe */
  }
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse {
    left: 0;
    width: 100%;
    padding: 0 15px;
    position: absolute;
    background: #FFFFFF;
  }
}

.main-menu .navbar-collapse li {
  float: left;
  padding: 34px 25px;
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
  position: relative;
}

@media only screen and (max-width: 1500px) {
  .main-menu .navbar-collapse li {
    padding: 34px 16px;
  }
}

@media only screen and (max-width: 1199px) {
  .main-menu .navbar-collapse li {
    padding: 30px 15px;
  }
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse li {
    float: none;
    padding: 0 20px;
    background: white;
    border-top: 1px solid #f3f3f3;
    /* lagbe */
  }
}

.main-menu .navbar-collapse li.dropdown .dropdown-btn {
  position: absolute;
  right: 10px;
  top: 0;
  width: 50px;
  height: 43px;
  border-left: 1px solid #f2f2f2;
  /* lagbe */
  text-align: center;
  line-height: 43px;
  cursor: pointer;
  display: none;
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse li.dropdown .dropdown-btn {
    display: block;
    background: white;
  }
}

.main-menu .navbar-collapse li a {
  position: relative;
  display: block;
  font-size: 16px;
  opacity: 1;
  font-weight: 500;
  color: #000000;
  text-transform: capitalize;
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse li a {
    padding: 10px 10px;
    line-height: 22px;
  }
}

.main-menu .navbar-collapse li a:hover,
.main-menu .navbar-collapse li.current a,
.main-menu .navbar-collapse li.current-menu-item a {
  color: #000000;
}

.main-menu .navbar-collapse li .btn-style-two {
  text-decoration: none;
  color: #000000;
}

.main-menu .navbar-collapse li .theme-btn {
  color: white;
  padding: 14px 20px;
  background: #000000;
}

.main-menu .navbar-collapse li .theme-btn:hover {
  color: white;
  background: #000000;
}

.main-menu .navbar-collapse li.for-mega {
  position: static;
  -webkit-animation-fill-mode: backwards;
  animation-fill-mode: backwards;
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse li.for-mega {
    position: relative;
  }
}

.main-menu .navbar-collapse li .megamenu {
  position: absolute;
  left: 0px;
  top: 100%;
  width: 100%;
  z-index: 100;
  display: none;
  padding: 20px 0;
  background: #ffffff;
  -webkit-box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.05), -2px 0px 5px 1px rgba(0, 0, 0, 0.05);
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.05), -2px 0px 5px 1px rgba(0, 0, 0, 0.05);
}

.main-menu .navbar-collapse li .megamenu:after {
  display: block;
  clear: both;
  content: "";
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse li .megamenu {
    position: relative;
    -webkit-box-shadow: none;
    box-shadow: none;
    width: 100%;
    padding: 0;
  }

  .main-menu .navbar-collapse li .megamenu .container {
    max-width: 100%;
  }

  .main-menu .navbar-collapse li .megamenu .row {
    margin: 0px;
  }
}

.main-menu .navbar-collapse li .megamenu+.dropdown-btn {
  z-index: 100;
}

.main-menu .navbar-collapse li .megamenu ul {
  display: block;
  position: relative;
  top: 0;
  width: 100%;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.main-menu .navbar-collapse li .megamenu .mega-title {
  margin-left: 20px;
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse li .megamenu .mega-title {
    margin-top: 12px;
    margin-left: 25px;
  }
}

.main-menu .navbar-collapse li ul {
  position: absolute;
  left: inherit;
  top: 100%;
  min-width: 220px;
  z-index: 100;
  background: #ffffff;
  -webkit-box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.05), -2px 0px 5px 1px rgba(0, 0, 0, 0.05);
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.05), -2px 0px 5px 1px rgba(0, 0, 0, 0.05);
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
  transform: scaleY(0);
  transform-origin: top center;
  opacity: 0;
  visibility: hidden;
}

.main-menu .navbar-collapse li:hover>ul {
  transform: scaleY(1);
  opacity: 1;
  visibility: visible;
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse li ul {
    position: relative;
    width: 100%;
    -webkit-box-shadow: none;
    box-shadow: none;
  }

  .main-menu .navbar-collapse li ul:after {
    display: block;
    clear: both;
    content: "";
  }
}

@media only screen and (max-width: 991px) and (max-width: 375px) {
  .main-menu .navbar-collapse li ul {
    min-width: 200px;
  }
}

.main-menu .navbar-collapse li ul li {
  width: 100%;
  padding: 7px 20px;
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse li ul li {
    padding: 0 15px;
  }
}

.main-menu .navbar-collapse li ul li ul {
  left: 100%;
  top: 0%;
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse li ul li ul {
    left: auto;
  }
}

.main-menu .navbar-header {
  display: none;
}

@media only screen and (max-width: 991px) {
  .main-menu .navbar-header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
